'use client'
import React, { useState, KeyboardEvent, FormEvent, useEffect } from "react";
import { Arrow } from "../icon/arrow";
import { useAppContext } from "@/app/context/AppContext";
import toast from 'react-hot-toast';
import axios from "axios";


interface Chat {
    _id: string;
    messages: Message[];
}

interface Message {
    role: string;
    content: string;
    timestamp?: number;
}

interface PromptBoxProps {
    isLoading: boolean;
    setIsLoading: (isLoading: boolean) => void;
}


interface ApiResponse {
    success: boolean;
    data?: {
        content: string;
    };
    message?: string;
}







export default function PromptBox({ isLoading, setIsLoading }: PromptBoxProps) {

    const [prompt, setPrompt] = useState<string>('');
    const { user, chats, setChats, selectedChat, setSelectedChat } = useAppContext();
    

    const sendMessages = async() => {
        if (!user) {
            toast.error("Please log in to send messages.");
            return;
        }
    
        if (isLoading) {
            toast.error("Please wait until the current message is processed.");
            return;
        }




    }
    

    return ( <div> 
        <div>
           
        </div>
     
        <form  className={`w-full ${false ? "max-w-3xl" : " max-w-2xl"} bg-white/10 backdrop-blur-md drop-shadow-lg border-white border-2 rounded-3xl mt-4 transition-all shadow-2xs p-4`}>
        <textarea
           
            className="outline-none overflow-hidden resize-none bg-transparent break-words w-full"
            rows={2}
            placeholder="Type here your fav lyrics"
            onChange={(e) => setPrompt(e.target.value)}
            value={prompt}
        />
        <div onClick={sendMessages()} className={`${prompt ? "bg-purple-800 text-white" : "bg-purple-200 text-purple-800"} flex ml-[590px] justify-end place-content-end p-2 rounded-full`}>
            <Arrow />
        </div>
    </form>
    
    </div>
      )



       
       
    

    }